// common mixins - start

@mixin qodefBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin qodefImageOverlayHoverStyle($with-hover: true) {
    
    @if ($with-hover) {
        
        &:hover {
            
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include qodefAbsoluteHolderLayout();
            content: '';
            background-color: rgba($default-heading-color, .55);
            opacity: 0;
            transition: opacity .2s ease-in-out;
        }

    } @else {
        @include qodefAbsoluteHolderLayout();
        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;
        transition: opacity .2s ease-in-out;
    }
}

@mixin qodefStandardPaginationStyle($list_type: null) {
    @include qodefRelativeHolderLayout(top);
    margin-top: -10px;
    clear: both;
    font-family: $default-heading-font;

    ul {
        @include qodefRelativeHolderLayout(top);
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: right;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 6px;

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0 6px;
                color: #bbb;

                &:hover{
                    color: #000;
                }
            }

            &.qodef-pag-active {
                
                a {
                    color: #000;
                }
            }

            &.qodef-pag-prev,
            &.qodef-pag-next,
            &.qodef-pag-first,
            &.qodef-pag-last {
                margin: 0 2px;

                a {
                    font-size: 20px;
                    color: #bbb;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                
                &.qodef-pag-prev {
                    
                    a {
                        opacity: 0;
                    }
                }

                &.qodef-pag-next {
                    
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: #000;
                }

                a {
                    
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            font-size: 16px;
                            line-height: 26px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-right: -10px;

                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        margin-left: -10px;

                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin qodefButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 16px;
    line-height: 2em;
    font-weight: 400;
    letter-spacing: 0;
    outline: none;
    box-sizing: border-box;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
}

@mixin qodefButtonSize($size: medium) {
    
    @if ($size == 'small') {
        padding: 6px 30px;

    } @else if ($size == 'medium') {
        padding: 8px 34px;

    } @else if ($size == 'large') {
        padding: 10px 38px;

    } @else if ($size == 'huge') {
        padding: 14px 53px;
    }
}

@mixin qodefButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin qodefButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: #000 $important;
    border: 1px solid transparent $important;
}

@mixin qodefButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: #1b1b1b $important;
}

@mixin qodefButtonOutlineColor($important: null) {
    color: $default-heading-color $important;
    background-color: transparent $important;
    border: 1px solid #ddd $important;
}

@mixin qodefButtonOutlineHoverColor($important: null) {
    color: $default-heading-color $important;
    background-color: transparent $important;
    border: 1px solid $default-border-color $important;
}

@mixin qodefPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// mixins styles - start

%input-style {
    position: relative;
    width: 100%;
    margin: 0 0 #{$input-margin}px;
    padding: #{$input-vertical-padding}px #{$input-horizontal-padding}px;
    font-family: inherit;
    font-size: 14px;
    font-weight: inherit;
    line-height: calc(#{$input-height}px - (#{$input-vertical-padding * 2}px - 2px));
    color: #000;
    background-color: transparent;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid #000;
    border-radius: 0;
    outline: 0;
    cursor: pointer;
    -webkit-appearance: none;
    box-sizing: border-box;
    transition: border-color 0.2s ease-in-out;

    &:focus {
        color: $default-heading-color;
        border-color: $default-text-color;
    }

    @include qodefPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

