////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// heading mixins - start

@mixin qodefH1() {
    font-size: 40px;
    line-height: 1.25em;
}

@mixin qodefH2() {
    font-size: 30px;
    line-height: 1.33em;
    font-family: $default-text-font;
    letter-spacing: 0;
    text-transform: none;
}

@mixin qodefH3() {
    font-size: 30px;
    line-height: 1.16em;
}

@mixin qodefH4() {
    font-size: 25px;
    line-height: 1.2em;
}

@mixin qodefH5() {
    font-size: 20px;
    line-height: 1.25em;
}

@mixin qodefH6() {
    font-size: 16px;
    line-height: 1.25em;
}

@mixin qodefHeadingStyle($with-heading: null, $with-color: true) {

    @if ($with-color) {
        color: $default-heading-color;
    }

    @if ($with-heading == 'h1') {
        @include qodefH1();
    } @else if ($with-heading == 'h2') {
        @include qodefH2();
    } @else if ($with-heading == 'h3') {
        @include qodefH3();
    } @else if ($with-heading == 'h4') {
        @include qodefH4();
    } @else if ($with-heading == 'h5') {
        @include qodefH5();
    } @else if ($with-heading == 'h6') {
        @include qodefH6();
    }
}

@mixin qodefDefaultHeadingStyle() {
    @include qodefHeadingStyle();
    font-family: $default-heading-font;
    margin: 25px 0;
    letter-spacing: 0.1em;
    font-weight: 400;
    text-transform: uppercase;

    -ms-word-wrap: break-word;
    word-wrap: break-word;

    a {
        @include qodefTypographyLayout();

        &:hover {
            color: $default-hover-color;
        }
    }
}

@mixin qodefBody() {
    font-family: $default-text-font;
    font-size: 14px;
    font-weight: 400;
    line-height: 23px;
    color: $default-text-color;
    background-color: $default-background-color;
    -webkit-font-smoothing: antialiased;
    letter-spacing: 0;
}

// heading mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

@mixin qodefUnderBackLine() {
    content: '';
    position: absolute;
    bottom: 4px;
    left: -3px;
    background: $first-main-color;
    height: 10px;
    width: 0;
    z-index: -1;
    transition: all .3s ease-out;
}

@mixin qodefBeforeLine() {
    content: '';
    position: absolute;
    width: 8px;
    height: 2px;
    background: currentColor;
    left: -20px;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0;
    transition: opacity .3s ease-out;
}

@mixin qodefImageZoomEffect() {
    transform: scale(1.04);
}

@mixin qodefImageZoomTransition() {
    transition: transform .5s ease-out;
}